	module.exports = {"data":{"markdownRemark":{"html":"<p><strong><a href=\"/?tag=reimu+contrafactum\">SS “Reimu”を読む (約2,100文字)</a> (猟奇的表現あり注意)</strong> 危険な詞を大量に生み出した白人ラッパー、EMINEM の曲の<em>中でも特にぶっ飛んじゃってる</em>曲、 “Kim” の替え歌(アルバム “The Marshall Mathers”より)。 EMINEM(Slim) を Marisa、 Kim を Reimu に置き換えて強引にストーリーを東方に置き換えてみました。</p>\n<h2>注意</h2>\n<p>この SS は<em>猟奇的なシーンを容易に想像できるセリフが含まれています</em>ので、そっち系の免疫のある方のみお読み戴くことをお勧めします。</p>\n<h2>口直し</h2>\n<p>……結局1時間後には魔理沙直々白玉楼に行って霊夢に謝りに行くのはお約束。3時間後には完全に仲直りしちゃってるのもお約束。1日後には生死の境界をいじくっていつも通りの日常に戻ってるのもお約束。ビバ妖怪の力。幻想郷は今日も平和。</p>","frontmatter":{"cover":null,"date":"2004-08-31T00:10:59+09:00","strDate":"2004/8/30","title":"Reimu"}}},"pathContext":{"path":"/ja/blog/2004/08/31-reimu/","langKey":"ja"}}

